* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

/* Design Tokens (CSS Variables) */
:root {
    /* Spacing */
    --ds-spacing-xxs: 4px;
    --ds-spacing-xs: 8px;
    --ds-spacing-sm: 12px;
    --ds-spacing-md: 16px;
    --ds-spacing-lg: 24px;
    --ds-spacing-xl: 32px;
    --ds-spacing-xxl: 48px;
    --ds-spacing-page-padding: var(--ds-spacing-lg); /* Default page padding */

    /* Colors */
    --ds-color-primary: #007bff;
    --ds-color-secondary: #6c757d;
    --ds-color-text: #333;
    --ds-color-background: #f8f9fa;

    /* Typography */
    --ds-font-family-body: sans-serif;
    --ds-font-family-heading: serif;
    --ds-font-size-base: 16px;

    /* Page specific */
    --ds-page-max-width: 1200px;
}

/* Global base styles */
html {
    font-family: var(--ds-font-family-body);
    font-size: var(--ds-font-size-base);
    line-height: 1.5;
    color: var(--ds-color-text);
    background-color: var(--ds-color-background);
}

body {
    margin: 0;
    padding: 0;
}

/* Utility classes */
.ds-text-center {
    text-align: center;
}

.ds-text-left {
    text-align: left;
}

.ds-text-right {
    text-align: right;
}

.ds-margin-0 {
    margin: 0;
}

.ds-padding-0 {
    padding: 0;
} `, "",{"version":3,"sources":["webpack://./src/design_system/styles.css"],"names":[],"mappings":"AAAA,4BAA4B;AAC5B;IACI,sBAAsB;IACtB,SAAS;IACT,UAAU;AACd;;AAEA,kCAAkC;AAClC;IACI,YAAY;IACZ,qBAAqB;IACrB,oBAAoB;IACpB,qBAAqB;IACrB,qBAAqB;IACrB,qBAAqB;IACrB,qBAAqB;IACrB,sBAAsB;IACtB,+CAA+C,EAAE,yBAAyB;;IAE1E,WAAW;IACX,2BAA2B;IAC3B,6BAA6B;IAC7B,qBAAqB;IACrB,8BAA8B;;IAE9B,eAAe;IACf,iCAAiC;IACjC,+BAA+B;IAC/B,yBAAyB;;IAEzB,kBAAkB;IAClB,2BAA2B;AAC/B;;AAEA,uBAAuB;AACvB;IACI,uCAAuC;IACvC,mCAAmC;IACnC,gBAAgB;IAChB,2BAA2B;IAC3B,4CAA4C;AAChD;;AAEA;IACI,SAAS;IACT,UAAU;AACd;;AAEA,oBAAoB;AACpB;IACI,kBAAkB;AACtB;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,SAAS;AACb;;AAEA;IACI,UAAU;AACd","sourcesContent":["/* CSS Reset/Normalization */\n* {\n    box-sizing: border-box;\n    margin: 0;\n    padding: 0;\n}\n\n/* Design Tokens (CSS Variables) */\n:root {\n    /* Spacing */\n    --ds-spacing-xxs: 4px;\n    --ds-spacing-xs: 8px;\n    --ds-spacing-sm: 12px;\n    --ds-spacing-md: 16px;\n    --ds-spacing-lg: 24px;\n    --ds-spacing-xl: 32px;\n    --ds-spacing-xxl: 48px;\n    --ds-spacing-page-padding: var(--ds-spacing-lg); /* Default page padding */\n\n    /* Colors */\n    --ds-color-primary: #007bff;\n    --ds-color-secondary: #6c757d;\n    --ds-color-text: #333;\n    --ds-color-background: #f8f9fa;\n\n    /* Typography */\n    --ds-font-family-body: sans-serif;\n    --ds-font-family-heading: serif;\n    --ds-font-size-base: 16px;\n\n    /* Page specific */\n    --ds-page-max-width: 1200px;\n}\n\n/* Global base styles */\nhtml {\n    font-family: var(--ds-font-family-body);\n    font-size: var(--ds-font-size-base);\n    line-height: 1.5;\n    color: var(--ds-color-text);\n    background-color: var(--ds-color-background);\n}\n\nbody {\n    margin: 0;\n    padding: 0;\n}\n\n/* Utility classes */\n.ds-text-center {\n    text-align: center;\n}\n\n.ds-text-left {\n    text-align: left;\n}\n\n.ds-text-right {\n    text-align: right;\n}\n\n.ds-margin-0 {\n    margin: 0;\n}\n\n.ds-pa